// filter based on checkboxes
import React, { createContext, useState, useEffect } from "react";
import examplesData from "./data";

export const ExamplesContext = createContext();

const ExamplesContextProvider = (props) => {
  const [examples, setExamples] = React.useState(examplesData);
  const [currentExamples, setCurrentExamples] = React.useState(examplesData);
  const [currentTypes, setCurrentTypes] = React.useState(["online", "webinar"]);

  function filterExamples(type, checked) {
    // setCurrentExample(currentExamples[index]);
    console.log(type);
    console.log(checked);
    // update array of filter tags
    // if true, add to array
    // if false, remove from array
  }

  function sortExamples(type) {
    //let tag = type;
    console.log(typeof type);
    const newCurrentExamples = currentExamples.sort((a, b) =>
      a.tag > b.tag ? 1 : -1
    );
    //console.log(newCurrentExamples);
    setCurrentExamples(newCurrentExamples);
    setExamples([newCurrentExamples]);
    //console.log("curr" + currentExamples);
    //tag = "";
  }

  return (
    <ExamplesContext.Provider
      value={{
        examples,
        currentExamples,
        filterExamples,
        sortExamples
      }}
    >
      {props.children}
    </ExamplesContext.Provider>
  );
};

export default ExamplesContextProvider;

