import React from "react";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import { ExamplesContext } from "./Context";

export default function SimpleMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const { sortExamples } = React.useContext(ExamplesContext);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (event) => {
    sortExamples(event.target.id);
    //console.log(event.target.id);
    setAnchorEl(null);
  };

  // how to add the ^ and opp to the button
  // to indicate open/closed

  return (
    <div className="sort-menu">
      <Button
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        Sort By
        <ArrowDropDownIcon />
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem id="title" onClick={handleClose}>
          A - Z
        </MenuItem>
        <MenuItem id="type" onClick={handleClose}>
          Type
        </MenuItem>
      </Menu>
    </div>
  );
}
